<template>
  <li
    class="hot-item"
    @click="$emit('updata-song', song)"
    :style="{ color: song.id === songId ? 'red' : '' }"
  >
    <div class="sgleft">
      <div class="list">
        <span> {{ (index + 1).toString().padStart(2, "0") }}</span>
      </div>
      <div class="info">
        <h4>{{ song.name }}</h4>
        <div class="left"></div>
        <span>{{ song.ar[0].name }} - {{ song.al.name }}</span>
      </div>
    </div>
    <div class="sgright" v-if="song.id !== songId"></div>
    <div class="sgright active" :class="{ playing: playing }" v-else>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    song: Object,
    index: Number,
    songId: [Number, String],
    playing: Boolean,
  },
};
</script>

<style lang="less" scoped>
@keyframes running {
  from {
    transform: rotateX(0deg);
  }
  to {
    transform: rotateX(60deg);
  }
}
li.hot-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 10rem;
  border-bottom: 1rem solid #ededed;

  .list {
    width: 40rem;
    font-size: 17rem;
    margin-left: -10rem;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
      color: #999;
    }
  }

  &:nth-child(1) span {
    color: #df3436;
  }
  &:nth-child(2) span {
    color: #df3436;
  }
  &:nth-child(3) span {
    color: #df3436;
  }

  .sgleft {
    padding: 8rem 0;
    display: flex;

    h4 {
      font-size: 17rem;
    }

    .info {
      .left {
        display: inline-block;
        width: 12rem;
        height: 8rem;
        margin-right: 4rem;
        background-image: url("../imgs/index_icon_2x.png");
        background-size: 166rem 97rem;
      }

      span {
        font-size: 12rem;
        color: #888;
      }
    }
  }

  .sgright {
    width: 22rem;
    height: 22rem;
    margin-right: 15rem;
    background-position: -24rem 0;
    background-image: url("../imgs/index_icon_2x.png");
    background-size: 166rem 97rem;

    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;

      span {
        width: 11%;
        height: 100%;
        background-color: red;
        animation: running 0.6s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;

        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }
      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
}
</style>
