<template>
  <div class="about">
    <div class="loading" v-show="loading">
      <img src="../imgs/loading.gif" alt="" />
    </div>
    <div class="hottop">
      <div class="mask"></div>
      <div class="wrap">
        <div class="item"></div>
        <span class="time">更新日期：{{ updataTime() }}</span>
      </div>
    </div>

    <ul class="hot-list" @click="$emit('updata-song-item', hotsongs)">
      <HotList
        v-for="(song, index) in hotsongs"
        :key="song.id"
        :song="song"
        :index="index"
        :songId="songId"
        :playing="playing"
        @updata-song="$emit('updata-song', song)"
      />
    </ul>
    <footer>
      <span>查看完整榜单</span>
    </footer>
  </div>
</template>

<script>
import HotList from "@/components/HotList.vue";

export default {
  name: "HotView",
  components: {
    HotList,
  },
  data: function () {
    return {
      hotsongs: [],
      loading: false,
    };
  },

  props: {
    songId: [Number, String],
    playing: Boolean,
  },

  methods: {
    updataTime: function () {
      var m = new Date().getMonth() + 1;
      var n = new Date().getDate();
      this.t = m + "月" + n + "日";
      return this.t;
    },
  },

  created() {
    this.loading = true;
    this.axios
      .get("https://apis.netstart.cn/music/playlist/track/all?id=3778678", {
        params: { limit: 20 },
      })
      .then((h) => {
        this.loading = false;
        this.hotsongs = h.data.songs;
      });
  },
};
</script>

